@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#ededed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.heading{
  font-family: 'Amatic SC', cursive;
  font-size:2em;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logoHolder{
    height:130px;
    background-color:#1E404E;
}

#logoImage{
    height:180px;
    border-radius:90px;
    z-index:999;

}

.navItem:hover{
    color:#1e404e;
}

.insta{
    color:ivory;
    transition:0.3s;
}
.insta:hover{
  color: #e44639;
  -webkit-transform:scale(1.3);
          transform:scale(1.3);
}

.facebook{
    color:ivory;
        transition:0.3s;
}
.facebook:hover{
    color:#4867aa;
    -webkit-transform:scale(1.3);
            transform:scale(1.3);
}

.pinterest{
    color:ivory;
    transition:0.3s;
}
.pinterest:hover{
    color:#bd081b;
    -webkit-transform:scale(1.3);
            transform:scale(1.3);
}

.socialLink:visited{
    text-decoration:none;
    color:ivory;
}

.navItem{
    color:ivory;
    text-decoration: none;
}


#introContainer{
    width:70%;
    margin:0 auto;
}

#introText{
    line-height: 1.4;
}
#shopCategoryContainer{
    width:70%;
    margin:0 auto;
}

#shopCategories{
    display:flex;
    flex-direction: row;
    justify-content: center;
}

.shopCategory{
    position:relative;
    width:30%;
    height:200px;
    margin:15px;    
    background-image: url('https://images.unsplash.com/photo-1533382433768-3d403240fa40?ixlib=rb-0.3.5&s=bd7a3d5a68e7aab801aafd691c0434ca&auto=format&fit=crop&w=300&q=80');
    background-position: center;
    background-size:cover;
    /* border-radius:10px; */
}


.categoryTitle{
    height:100%;
    /* background-color:rgba(0,0,0,0.3);     */
    /* border-radius:10px; */
}

.categoryTitle img{
    display:block;
    width:100%;
    height:100%;
}

.viewButton{
    position:absolute;
    left:0;
    bottom:0;
    width:100%;
    height:25px;
    color:ivory;
    background-color:rgba(0,0,0,0.5);
    border:#5b7d76;
}

.viewButton span{
    color:white;
    font-size:1.4em;
}


#seperationBanner{
    position:relative;
    /* background-image:linear-gradient(#15303b,#172f39,#152d37,#142a35,#132730,#11242b,#10232a);
    background-attachment: fixed;
    height:312px; */
    margin-top:30px;
    margin-bottom:30px;
   
}

#seperationBannerInner{
    background-image: url(/static/media/banner.2066997b.png);
    background-position: center;
    background-repeat: no-repeat;
    height:312px;
    width:100%;
    background-size:stretch;

}
/*Blog Posts Container*/
#blogPosts{
    width:70%;
    display:flex;
    margin:0 auto;
    justify-content: space-evenly;
}

.post{
    background-color:white;
    width:30%;
    margin:10px;
}

.postImage{
    display:block;
    max-width:100%;
    width:100%;
    padding:0;
    max-height:200px;
}

.postTitle{
    display:block;
    font-size:1em;
    color: #12bbc2;

}

.postBody{
    text-align:left;
    font-size:0.9em;
    padding:5px;
    height:70px;
    white-space: pre-wrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.viewPostLink {
    padding:5px;
    text-decoration: none;
    font-size:1em;
    text-align: right;

}

.viewPostLink a{
    text-decoration: none;
    color: #12bbc2;
    font-size:0.8em;
}
.viewPostLink:visited{
    text-decoration: none;
}
#footer{
    /* background-image: url(../../images/footerBackground.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height:312px; */
    background-color:grey;
    height:300px;
}
.productDisplayContainer{
    position:relative;
    display:flex;
    width:80%;
    margin:0 auto;
}

.products{
    display:flex;
    justify-content: space-between;
    flex-wrap:wrap;
    width:70%;
}

.shopItem{
    position:relative;
    width:45%;
    /* height:500px; */
    margin-left:10px;
    margin-right:10px;
    margin-bottom:15px;
    background-color:white;

}
.shopItemImage{
    max-width:100%;
}
.shopItemName{
    font-family: 'Amatic SC', cursive;
    font-weight:bold;
    font-size:2em;
}

.shopItemPrice{
    font-size:1.2em;
}

.shopItemDescription{
    position:relative;
    height:90px;
}

.lower{
    position:relative;
    bottom:0;
    width:100%;
    display:flex;
    justify-content: space-between;
}




/*Place order Button*/
.placeOrder{
    /* position:absolute; */
    bottom:0;
    right:0;
}

.placeOrder button{
    padding:10px;
    background-color:#1e404e;
    border-radius:5px;
    border:none;
    color:white;
}

.placeOrder button:hover{
    
    background-color:#5b7d76;
   }


/*Sharing buttons*/
.sharing{
    /* position:absolute; */
    bottom:0;
    left:0;
}
.shareicon{
    margin-right:7px;
    padding:2px;
    font-size: 1.4em;
}

.pinterestShare{
    color:#bd081b;
}

.facebookShare{
    color: #4867aa;
}

.emailShare{
    color:#cd5c2f;
}


/*Product Filter*/

.filter{
    position:absolute;
    right:0;
    width:200px;
    box-shadow: 5px 10px 15px#888888;
    background-color:#1e404e;

}

.filterInner{

    display:flex;
    max-width:100%;
    min-width:100%;
    flex-direction:column;
    align-items:start;
    background-color:#1e404e;
    
}

#allFilter:hover{
    background-color:#5b7d76;
    cursor:pointer;
}

#charmFilter:hover{
    background-color:#5b7d76;
    cursor:pointer;
}

#stringArtFilter:hover{
    background-color:#5b7d76;
    cursor:pointer;
}

#dreamcatcherFilter:hover{
    background-color:#5b7d76;
    cursor:pointer;
}
.filterIcon:hover{
color:pink;
}


.filterHeadingContainer{

}

.filterHeading{
    display:block;
    font-weight:bold;
    padding-left:5px;
    padding-bottom:5px;

}


.subheadingContainer{
    display:flex;
    align-items:center;
    position:relative;
    padding-top:15px;
    padding-bottom:15px;
    text-align:left;
    border:1px solid white;
    width: 100%;

}


.filterSubheading{
    font-size:0.9em;
    color:white;

}

.filterIcon{
    font-size:1.7em;
    margin-right:10px;
    margin-left:5px;
    color:white;
}
